@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;400;600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Overpass+Mono:wght@300;400;600;700&display=swap);
html,
body {
  margin: 0;
  padding: 0;
  background-color: #B0D0E8;
  color: #000;
}

a {
  text-decoration: none;
}

.Navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: fixed;
  height: 80px;
  background-color: rgb(7, 160, 195, 0.6);
  top: 0;
  left: 0;
  right: 0;
  padding: 0 100px;
  overflow-x: hidden;

  font-size: 22px;
  font-weight: 600;
  font-family: 'Open Sans', sans-serif;
}
.Navbar a {
  padding: 5px;
  color: #000;
}

.resume {
  border-radius: 10px;
  background-color: rgba(0, 0, 0, 0.4);
  margin-left: 10px;
}
#Home {
  width: 100vw;
  height: 100vh;
  background-size: 100%;
  background-repeat: no-repeat;

  display: flex;
  align-items: center;
}

.home-text {
  font-family: 'Overpass Mono', monospace;
  margin: 150px;
  font-size: 45px;
  width: 50%;
}

.home-text p:nth-child(3) {
  font-size: 25px;

}
#About {
  height: 100vh;
  align-items: center;
  justify-content: center;
}
#About > div {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}

.about-display {
  display: flex;
}
.about-img {
  width: 300px;
  height: 300px;
  border-radius: 10px;
  background-color: #fff;
  margin-right: 50px;
}
.about-pg {
  font-family: 'Open Sans', sans-serif;
  font-size: 20px;
  max-width: 600px;
}
#Experiences {
  height: 100vh;
  font-size: 20px;

  font-family: 'Open Sans', sans-serif;
  align-items: center;
}

#Experiences > div {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.table {
  display: flex;
}
.left-section {
  max-width: 300px;
  margin-right: 20px;
  display: flex;
  flex-direction: column;
}
.right-section {
  max-width: 750px;
  color: #000;
  text-align: center;
  background-color: rgb(255, 255, 255, 0.4);
  border: 5px solid #5E60CE;
  border-radius: 10px;
  padding: 30px;
  height: 400px;
}

.ex-btn {
  text-align: center;
  border-radius: 5px;
  padding: 6px;
  cursor: pointer;
}

hr {
  visibility: hidden;
}
.hr {
  visibility: visible;
  background-color: #5E60CE;
  border: none;
  width: 80px;
  height: 5px;
}
.active {
  color: #000;
  font-weight: 600;
}

.desc {
  text-align: left;
}
#Projects {
  min-height: 100vh;

  display: flex;
  align-items: center;
}

#Projects > div {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.proj {
  display: flex;
  height: 600px;
}
.proj-img {
  height: 500px;
  min-width: 750px;
  overflow: hidden;
  border: 5px solid #5E60CE;
}
.proj-img img {
  height: 100%;
  min-width: 100%;
}

.proj-text-overlay {
  width: 450px;
  height: 500px;
  background-color: rgba(255, 255, 255, 0.5);

  position: relative;
  right: 20px;
  top: 5px;

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.proj-text-overlay-switch {
  left: 20px;
}

.proj-name {
  font-size: 30px;
  font-family: 'Overpass Mono', monospace;
}
.proj-lan {
  font-size: 20px;
}
.proj-desc {
  font-size: 20px;
  padding: 40px;
}

.hidden {
  display: none;
}

.proj-links {
  display: flex;
  justify-content: flex-end;
  margin-right: 20px;
  height: 30px;
}
.proj-links-switch {
  justify-content: flex-start;
  margin-left: 20px;
}

.proj-links a {
  margin: 0 10px;
}

.show-more {
  display: flex;
  justify-content: center;
}
.btn {
  cursor: pointer;
  font-size: 20px;
  font-family: 'Open Sans', sans-serif;
  padding: 7px;
  border-radius: 10px;
  border: none;
  background-color: #5E60CE;
  opacity: 0.8;
  margin-bottom: 50px;
}

#Contact {
  height: 100px;
  padding: 0 100px;

  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: rgb(7, 160, 195, 0.8);
}

#Contact div {
  display: flex;
}

#Contact span {
  font-family: 'Overpass mono', monospace;
}
.contact-links {
  width: 40px;
  height: 40px;
  margin: 10px;
}

.contact-links img {
  width: 100%;
}
.App {
  overflow-x: hidden;
  width: 100vw;
}

#About, 
#Experiences, 
#Projects {
  padding: 0 100px;

  display: flex;
  flex-direction: column;
}

.section-title {
  font-size: 40px;
  font-family: 'Overpass Mono', monospace;
  margin: 100px 0 20px;
}
