#Projects {
  min-height: 100vh;

  display: flex;
  align-items: center;
}

#Projects > div {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.proj {
  display: flex;
  height: 600px;
}
.proj-img {
  height: 500px;
  min-width: 750px;
  overflow: hidden;
  border: 5px solid #5E60CE;
}
.proj-img img {
  height: 100%;
  min-width: 100%;
}

.proj-text-overlay {
  width: 450px;
  height: 500px;
  background-color: rgba(255, 255, 255, 0.5);

  position: relative;
  right: 20px;
  top: 5px;

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.proj-text-overlay-switch {
  left: 20px;
}

.proj-name {
  font-size: 30px;
  font-family: 'Overpass Mono', monospace;
}
.proj-lan {
  font-size: 20px;
}
.proj-desc {
  font-size: 20px;
  padding: 40px;
}

.hidden {
  display: none;
}

.proj-links {
  display: flex;
  justify-content: flex-end;
  margin-right: 20px;
  height: 30px;
}
.proj-links-switch {
  justify-content: flex-start;
  margin-left: 20px;
}

.proj-links a {
  margin: 0 10px;
}

.show-more {
  display: flex;
  justify-content: center;
}
.btn {
  cursor: pointer;
  font-size: 20px;
  font-family: 'Open Sans', sans-serif;
  padding: 7px;
  border-radius: 10px;
  border: none;
  background-color: #5E60CE;
  opacity: 0.8;
  margin-bottom: 50px;
}
